<template>
  <div class="cs-header" :style="{height:height+'px'}">
    <div @click="onClickBack" v-if="showBack" class="cs-back-div">
      <div class="cs-left-arrow"></div>
      <span class="cs-left-span" v-if="showBackText">{{backText}}</span>
    </div>
    <span class="cs-header-title">{{title}}</span>
  </div>
</template>

<script>
  export default{
    name: 'Cheader',
    mounted (){

    },
    props: {
      title: '',
      height: '',
      backText: {
        type: String,
        default: '返回'
      },
      showBack: false,
      showBackText: false,
      preventGoBack: false
    },
    data: function () {
      return {

      }
    },
    methods:{
      onClickBack(){
        if(this.preventGoBack){
          this.$emit('on-click-back','a');
        }else{
          this.$router ? this.$router.back() : window.history.back();
        }
      }
    }
  }
</script>

<style lang="less">
  @import "head.less";
</style>



