<template>
  <div class="body_div">
    <search :data="list" @changeData="changeList"></search>
    <div class="foreach_div" v-for="l in list">
      {{l.name}}
    </div>
  </div>
</template>

<script>
  import json from '../data/data.json'
  import search from '../components/Search'

  export default {
    components: {
      search
    },
    created() {
      this.list = json;
    },
    data() {
      return {
        list: ''
      }
    },
    methods: {
      changeList(data) {
        if (data != undefined) {
          this.list = data;
        }
      }
    }
  }
</script>

<style scoped>
  .body_div {
    height: 100%;
    overflow-y: scroll;
    padding-bottom: 5px;
  }

  .foreach_div {
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #878787;
    padding-left: 5px;
  }

  /*.foreach_div:nth-child(2) {*/
  /*  border-top: 1px solid #878787;*/
  /*}*/
</style>



