<template>
  <div class="container">
    <CHeader @on-click-back="back" :preventGoBack="preventGoBack" :title="msg" :height="height" :showBack="showBack"
             :showBackText="showBackText"></CHeader>
    <div class="swipe">
      <CSwipe :speed="speed">
        <CSwipeItem>
          <a href="javascript:"><img src="../assets/slided.jpg"/></a>
        </CSwipeItem>
        <CSwipeItem>
          <a href="javascript:"><img src="../assets/slidee.jpg"/></a>
        </CSwipeItem>
        <CSwipeItem>
          <a href="javascript:"><img src="../assets/slidec.jpg"/></a>
        </CSwipeItem>
      </CSwipe>
    </div>
  </div>
</template>

<script>
  export default{
    components: {},
    mounted(){

    },
    data(){
      return {
        msg: '这是一个标题这是一个标题这是一个标题这是一个标题',
        height: 50,
        showBack: false,
        showBackText: true,
        preventGoBack: true,
        speed: 5000
      }
    },
    methods: {
      back(){
        alert('返回事件');
      }
    }
  }
</script>

<style>
  .swipe {
    height: 25vh;
  }
</style>



