<template>
    <div>
      <div v-for="l in list">
        {{l.name}}
      </div>
      <CenterDiv>
        <CButton @click="add" :display="displayVal"></CButton>
      </CenterDiv>
      <br>
      <br>
      <br>
      <br>
      {{obj}}
      <CenterDiv>
        <CButton @click="setObj" :display="displayVal" style="background: red;"></CButton>
      </CenterDiv>
    </div>
</template>

<script>

  export default{
    mounted(){

    },
    methods:{
      add(){
        let obj = {
          name: 'lc'
        }
        this.list[3] = obj;  //视图不会更新
        this.$set(this.list,3,obj); //视图会更新
        console.log(this.list);
        alert('clicking');
      },
      setObj(){
//        this.obj.age = 12;  //页面不会更新
//        console.log(this.obj);
        this.$set(this.obj,'age',12); //使用set方法会刷新
        console.log(this.obj);
      }
    },
    data(){
      return {
        list: [
          {
            name: 'zhansan'
          },
          {
            name: 'lisi'
          }
          ,
          {
            name: 'wangwu'
          }
        ],
        displayVal: false,
        obj:{
          name: '李四',
          sex: '男',
          wife: '王兔兔'
        }
      }
    }
  }
</script>





