<template>
  <div class="container">
    <div class="example">
      <CButton @click="clickone" type="default">默认按钮</CButton>
    </div>
    <div class="example">
      <CButton type="primary">主要按钮</CButton>
    </div>
    <div class="example">
      <CButton type="message">信息按钮</CButton>
    </div>
    <div class="example">
      <CButton type="danger">危险按钮</CButton>
    </div>
    <div class="example">
      <CButton type="warning">警告按钮</CButton>
    </div>
    <div class="example">
      <CButton @click="clickone" type="primary" disabled="disabled">禁用按钮</CButton>
    </div>
    <div class="example">
      <CButton @click="clicktwo" type="primary" size="small">小尺寸</CButton>
    </div>
    <div class="example">
      <CButton @click="clicktwo" type="message" size="large">大尺寸</CButton>
    </div>
    <br/>
  </div>
</template>

<script>
  export default{
    created(){

    },
    mounted(){

    },
    data(){
      return {}
    },
    methods: {
      clickone() {
        console.log('点击了1');
      },
      clicktwo() {
        console.log('点击了2');
      }
    }
  }
</script>

<style scoped>
  .example {
    margin: 10px;
  }
</style>



