import Vue from 'vue'
import Router from 'vue-router'
import headView from '@/page/headView'
import sets from '@/page/set'
import searchView from '@/page/searchView'
import buttonView from '@/page/buttonView'
import index from '@/page/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/headView',
      name: 'headView',
      component: headView
    },
    {
      path: '/set',
      name: 'set',
      component: sets
    },
    {
      path: '/searchView',
      name: 'searchView',
      component: searchView
    },
    {
      path: '/buttonView',
      name: 'buttonView',
      component: buttonView
    }
  ]
})



