// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Cheader from './components/CHeader/Head.vue'
import CenterDiv from './components/CenterDiv.vue'
import CButton from './components/CButton/Button.vue'
import CSwipe from './components/CSwipe/Swipe.vue'
import CSwipeItem from './components/CSwipeItem/SwipeItem.vue'

Vue.config.productionTip = false;
Vue.component('CHeader',Cheader);
Vue.component('CenterDiv',CenterDiv);
Vue.component('CButton',CButton);
Vue.component('CSwipe',CSwipe);
Vue.component('CSwipeItem',CSwipeItem);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



