<!-- 组件引用形式 -->
<template>
  <input v-model="inputVal" class="default_input" @input="input" placeholder="请输入..."></input>
</template>

<script>
  export default {
    components: {},
    created() {
      this.list = this.data;  //list 代表筛选时用的数组
      this.primitiveList = this.data; //primitiveList 保留一份原始数据,输入框空时重置数据
    },
    mounted() {

    },
    props: {
      data: {
        type: Array,
        default: []
      }
    },
    data() {
      return {
        list: [],
        primitiveList: [],
        inputVal: ''
      }
    },
    methods: {
      input() {
        console.log(this.inputVal);
        if (this.inputVal == '') {
          this.$emit('changeData', this.primitiveList);
        } else {
//          this.$vux.loading.show({
//            text: '搜索中...'
//          })
          this.query(this.inputVal).then(data => {
            if (data != undefined) {
//              this.$vux.loading.hide();
              this.$emit('changeData', data);
            }
          })
        }
      },
      query(v) {
        let promise = new Promise((resolve, reject) => {
          let array = this.list;
          let newArray = [];
          for (let i of array) {
            // if (i.name.includes(v)) {
            //   newArray.push(i);
            // }
            if (i.name.indexOf(v) !== -1) {
              newArray.push(i);
            }
          }
          resolve(newArray);
        })
        return promise;
      }
    }
  }
</script>
<style scope>
  .default_input {
    width: 100%;
    outline: none;
    font-size: 15px;
    border: none;
    height: 50px;
    padding-left: 12px;
    border-bottom: 1px solid #878787;
  }
</style>




