<template>
  <div class="container">
    <CHeader @on-click-back="back" :preventGoBack="preventGoBack" :title="msg" :height="height" :showBack="showBack"
             :showBackText="showBackText"></CHeader>
  </div>
</template>

<script>
  export default{
    components: {},
    mounted(){

    },
    data(){
      return {
        msg: '这是一个标题这是一个标题这是一个标题这是一个标题',
        height: 50,
        showBack: true,
        showBackText: true,
        preventGoBack: true
      }
    },
    methods: {
      back(){
        alert('返回事件');
      }
    }
  }
</script>

<style>

</style>



