<template>
  <div class="cs-main-slide">
    <slot></slot>
    <div class="cs-slide-round">
      <i ref="cs" v-for="s,key in slideLength" :key="key"></i>
    </div>
  </div>
</template>

<script>
  export default{
    created(){

    },
    mounted(){
      let slide_div = this.$children;
      this.slideLength = slide_div.length;
      let width = window.innerWidth;
      let num = 1;
      let self = this;
      for (let i = 0; i < slide_div.length; i++) {
        if (i == 0) {
          slide_div[i].$el.setAttribute('moveNum', '0');
          slide_div[i].$el.setAttribute('style', `transition-duration: ${self.transitionSpeed}ms; transform: translateX(0px);`);
        } else {
          let moveNum = i * width * num;
          slide_div[i].$el.setAttribute('moveNum', moveNum);
          slide_div[i].$el.setAttribute('style', `transition-duration: ${self.transitionSpeed}ms; transform: translateX(${moveNum}px);`);
        }
      }

      let count = 0;
      let moveNum = 0;
      let lastCount = 0;
      setTimeout(function () {
        self.$refs.cs[0].classList.add('i-active');
      }, 10);
      setInterval(function () {
        if (count == slide_div.length - 1) {
          // console.log('重置');
          count = 0;
          self.removeClass(self.$refs.cs);
          self.$refs.cs[count].classList.add('i-active');
          for (let i = 0; i < slide_div.length; i++) {
            if (i == 0) {
              slide_div[i].$el.setAttribute('moveNum', '0');
              slide_div[i].$el.setAttribute('style', `transition-duration: ${self.transitionSpeed}ms; transform: translateX(0px);`);
            }
            else {
              let moveNum = i * width * num;
              slide_div[i].$el.setAttribute('moveNum', moveNum);
              slide_div[i].$el.setAttribute('style', `transition-duration: ${self.transitionSpeed}ms; transform: translateX(${moveNum}px);`);
            }
          }
        } else {
          // console.log('下一张');
          ++count;
          if (self.$refs.cs != undefined) {
            self.removeClass(self.$refs.cs);
            if (self.$refs.cs[count].classList != undefined) {
              self.$refs.cs[count].classList.add('i-active');
            }
          }

          let basicNum = slide_div[count].$el.getAttribute('moveNum');
          moveNum = count * width * num;
          let newNum = basicNum - moveNum;
          // console.log('newNum', newNum);
          slide_div[count].$el.setAttribute('style', `transition-duration: ${self.transitionSpeed}ms; transform: translateX(${newNum}px);`);
        }
      }, this.speed);
    },
    props: {
      speed: {
        type: Number,
        default: 3000
      }
    },
    data: function () {
      return {
        slideLength: 0,
        transitionSpeed:200
      }
    },
    methods: {
      removeClass(array){
        array.forEach(e => {
          if (e != undefined && e.classList != undefined) {
            e.classList.remove('i-active');
          }
        })
      }
    }
  }
</script>

<style lang="less">
  @import "swipe.less";
</style>



